						<van-field v-model="records.data.back" label="立减(元)" placeholder="未设置" type="number" input-align="right" />
						<van-field v-model="records.data.agent_charge" label="代理商承担" placeholder="未设置" type="number" input-align="right" />
						<van-field v-model="records.data.plateform_charge" label="平台承担" placeholder="未设置" type="number" input-align="right" />
					</van-cell-group>
					<div class="remove-activity">删除</div>
				</template>
				<!--<div class="tip">下单返现</div>-->
				<!--<van-cell-group>-->
					<!--<van-field label="消费满(元)" placeholder="未设置" type="number" input-align="right" />-->
					<!--<van-field label="返(元)" placeholder="未设置" type="number" input-align="right" />-->
					<!--<van-field label="代理商承担" placeholder="未设置" type="number" input-align="right" />-->
