				<!--<van-cell-group>-->
					<!--<van-field label="消费满(元)" placeholder="未设置" type="number" input-align="right" />-->
					<!--<van-field label="赠送" placeholder="未设置" type="text" input-align="right" />-->
				<!--</van-cell-group>-->
				<!--<div class="remove-activity">删除</div>-->
				<!--<div class="tip">自提优惠</div>-->
				<!--<van-cell-group>-->
					<!--<van-field label="消费满(元)" placeholder="未设置" type="number" input-align="right" />-->
					<!--<van-field label="打(折)" placeholder="未设置" type="number" input-align="right" />-->
					<!--<van-field label="代理商承担" placeholder="未设置" type="number" input-align="right" />-->
