				<!--<div class="tip">满减配送费</div>-->
				<!--<van-cell-group>-->
					<!--<van-field label="消费满(元)" placeholder="未设置" type="number" input-align="right" />-->
					<!--<van-field label="减(元)" placeholder="未设置" type="number" input-align="right" />-->
					<!--<van-field label="代理商承担" placeholder="未设置" type="number" input-align="right" />-->
					<!--<van-field label="平台承担" placeholder="未设置" type="number" input-align="right" />-->
				<!--</van-cell-group>-->
				<!--<div class="remove-activity">删除</div>-->
				<!--<div class="tip">自提满减优惠</div>-->
				<!--<van-cell-group>-->
					<!--<van-field label="消费满(元)" placeholder="未设置" type="number" input-align="right" />-->
					<!--<van-field label="减(元)" placeholder="未设置" type="number" input-align="right" />-->
					<!--<van-field label="代理商承担" placeholder="未设置" type="number" input-align="right" />-->
					<!--<van-field label="平台承担" placeholder="未设置" type="number" input-align="right" />-->
				<!--</van-cell-group>-->
				<!--<div class="remove-activity">删除</div>-->
			</div>
			<div class="append-activity border-1px-tb">
				<i class="icon icon-add"></i>
				新建一级优惠
			</div>
			<div class="btn-group van-col">
				<button class="bg-primary van-button van-button--default van-button--normal van-button--block">
					<span class="van-button__text">保存</span>
				</button>
			</div>
		</div>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import {getData} from "@/controller/funcCommon"
	export default {
		data() {
			return {
				records:{
					type: '',
					data: []
				}
			}
		},
		components: {
			publicHeader,
		},
		methods: {
			onLoad() {
				var that = this;
				getData({
					vue: this,
					data: {
						id: that.id
					},
					url: 'plateform/merchant/activity/post',
					success: (res) => {
						that.records.data = [...that.records.data, ...res.records.data];
						that.records.type = res.records.type;
						console.log(that.records.data);
					}
				});
			},
		},
		created() {
			if(this.$route.query && this.$route.query.id > 0) {
				this.id = parseInt(this.$route.query.id)
			}
		},
		mounted(){
			this.onLoad();
		}
	}
</script>

<style>
	#activity-post .content .activity-group{
		margin-bottom: 15px;
	}
	#activity-post .content .activity-group .tip{
		padding: 10px 15px;
		font-size: 14px;
		color: #7a7a7a;
	}
	#activity-post .content .activity-group .remove-activity{
		padding: 10px 15px;
		text-align: center;
		line-height: 24px;
		color: #FF2C4C;
		font-size: 16px;
		background-color: #fff;
	}
	#activity-post .content .append-activity{
		padding: 10px 15px;
		text-align: center;
		line-height: 24px;
		color: #4FAE52;
		font-size: 15px;
		background-color: #fff;
	}
	#activity-post .content .btn-group{
		padding: 15px;
		width: 100%;
	}
</style>


